@import '../../Shared/varbs.scss';

.homeContainer {
  background    : linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(../../assets/header-Image.jpg) no-repeat center/cover;
  width         : 100%;
  height        : 900px;
  display       : flex;
  flex-direction: column;
  align-items   : center;

  @media screen and (max-width: $media-sm) {
    height: 700px;
  }
}

.descriptionContainer {
  width: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.catchPhrase {
  max-width: 700px;
  height: auto;
  font-size: 5rem;
  font-weight: bolder;
  line-height: 5rem;
  color: $whiteColor;
  @media screen and (max-width: $media-lg){
      max-width: 500px;
      font-size: 3rem;
      line-height: 3rem;
  }
}

.catchPhrasePragraph{
  font-size: 1.2rem;
  font-weight: lighter;
  color: $whiteColor;
  @media screen and (max-width: $media-xs){
    max-width: 300px;
    align-self: center;
    
}
}